import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
    meta: {
      title: "",
    },
  },
  {
    path: "/menu",
    name: "menu",
    meta: {
      title: "菜單",
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/MenuView.vue"),
  },
  {
    path: "/message",
    name: "message",
    meta: {
      title: "留下訊息",
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/MessageView.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const modeName = process.env.VUE_APP_TITLE;
  const pageName = to.meta.title || "阿豪海鮮熱炒";
  document.title =
    modeName === "正式版" ? pageName : `[${modeName}] ${pageName}`;
  next();
});

export default router;
